<template>
  <div class="shipping">
    <section class="container" style="padding-left:10px;">
      <el-switch v-model="enableShipping" active-text="外卖配送服务" />
    </section>
    <!-- 自配送配送费设置 -->
    <section class="container" v-if="enableShipping">
      <el-form :model="store" label-width="100px" class="form">
        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label="起送金额">
              <el-input v-model="store.shippingminamount" type="number">
                <template slot="append"
                  >元</template
                >
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label="最大配送距离">
              <el-input v-model="store.distancelimited" type="number" placeholder="0则表示不限制">
                <template slot="append"
                  >km</template
                >
              </el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="24">
            <el-form-item label="距离用时">
              <div class="shipping-fee-line" v-for="item of store.distancepricelistList" :key="item.distance">
                <span class="fee-desc-part" style="margin-right: 20px;">{{ item.distance !== 10000 ? '小于' + (item.distance + 'km') : '大于10km' }}</span>
                <span class="fee-desc-part" style="margin-right: 20px;">配送费</span>
                <el-input class="inline-form-item" v-model="item.price">
                  <template slot="append"
                    >元</template
                  >
                </el-input>
                <span class="fee-desc-part" style="margin-right: 20px;">需用时</span>
                <el-select class="inline-form-item" placeholder="请选择用时" v-model="item.duration">
                  <el-option label="15分钟" :value="900"></el-option>
                  <el-option label="30分钟" :value="1800"></el-option>
                  <el-option label="45分钟" :value="2700"></el-option>
                  <el-option label="1小时" :value="3600"></el-option>
                  <el-option label="1.5小时" :value="5400"></el-option>
                  <el-option label="2小时" :value="7200"></el-option>
                  <el-option label="3小时" :value="10800"></el-option>
                  <el-option label="1天" :value="86400"></el-option>
                </el-select>
              </div>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div class="save-btn-container">
        <el-button type="primary" @click="updateStore">
          保存
        </el-button>
      </div>
    </section>
  </div>
</template>

<script>
/* eslint-disable */
import { rpc } from '../../../service/index';
import { M } from '../../../service/grpc';
import { getToken } from '../../../service/helper';

const distancepricelistList = [
  {
    distance: 2,
    price: 0,
    duration: 1800
  },
  {
    distance: 5,
    price: 0,
    duration: 1800
  },
  {
    distance: 10,
    price: 0,
    duration: 7200
  },
  {
    distance: 10000,
    price: 0,
    duration: 86400
  }
];
export default {
  name: 'shipping',
  data() {
    return {
      token: getToken(),
      store: {},
      enableShipping: false
    };
  },
  watch: {
    enableShipping(v) {
      this.updateStore();
    }
  },
  mounted() {
    rpc('StoreAuthService.GetStore', M('Token', { token: this.token })).then(res => {
      res.shippingminamount = res.shippingminamount / 100;
      res.distancelimited = res.distancelimited / 1000;
      res.distancepricelistList = res.distancepricelistList.map(v => ({
        ...v,
        distance: v.distance / 1000,
        price: v.price / 100
      }));
      res.distancepricelistList = res.distancepricelistList.length ? res.distancepricelistList : distancepricelistList;
      this.store = res;
      this.enableShipping = this.store.deliverkindlistList.includes(2);
    });
  },
  methods: {
    updateStore() {
      const params = JSON.parse(JSON.stringify(this.store));
      params.shippingminamount = parseInt(params.shippingminamount * 100);
      params.distancelimited = parseInt(params.distancelimited * 1000);
      params.distancepricelistList = params.distancepricelistList.map(v => ({
        ...v,
        distance: parseInt(v.distance * 1000),
        price: parseInt(v.price * 100)
      }));
      if (this.enableShipping && !params.deliverkindlistList.includes(2)) {
        params.deliverkindlistList.push(2);
      }
      if (!this.enableShipping && params.deliverkindlistList.includes(2)) {
        params.deliverkindlistList = params.deliverkindlistList.filter(v => v !== 2);
      }
      rpc(
        'StoreStoreService.UpdateInfo',
        M('StoreWithTokenRequest', {
          token: M('Token', { token: this.token }),
          store: M('Store', {
            ...params,
            businesshoursList: params.businesshoursList.map(v => M('BusinessHours', v)),
            distancepricelistList: params.distancepricelistList.map(v => M('DistancePrice', v))
          })
        })
      ).then(res => {
        res.shippingminamount = res.shippingminamount / 100;
        res.distancelimited = res.distancelimited / 1000;
        res.distancepricelistList = res.distancepricelistList.map(v => ({
          ...v,
          distance: v.distance / 1000,
          price: v.price / 100
        }));
        this.store = res;
      });
    }
  }
};
</script>

